@import "@sass/_colors.scss";
@import "@sass/_variables.scss";

html,
body,
#root {
	font-family: $primary-font, sans-serif;
	overflow: hidden;
	min-width: 100vw;
	min-height: 100vh;
	margin: 0;
	padding: 0;
}

*,
*::after,
*::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	border: none;
	font-family: $secondary-font;
}

a,
a:visited,
a:focus,
a:active,
a:hover {
	color: inherit;
	text-decoration: none;
}

a:focus,
a:active {
	outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
label {
	font-size: inherit;
	font-weight: inherit;
	line-height: inherit;
}

ol,
ul,
li {
	margin: 0;
	padding: 0;
	list-style: none;
}

/**
* Override Firefox's stylesheet for red box shadow on invalid fields
* @see view-source:resource://gre-resources/forms.css
*/

:not(output):-moz-ui-invalid {
	box-shadow: none;
}
:not(output):-moz-ui-invalid:-moz-focusring {
	box-shadow: none;
}

img,
svg {
	max-width: 100%;
	vertical-align: middle;
	height: auto;
}

input,
textarea,
select {
	font-family: inherit;
	font-size: inherit;
}

input[type="submit"],
button {
	display: inline-block;
	background-color: transparent;
	background: none;
	cursor: pointer;
}

input:focus,
input:active,
button:focus,
button:active {
	outline: none;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}
